<template>
    <div>
        <home-header></home-header>
        <home-swiper :imgList="swiperList"></home-swiper>
        <home-icons :iconList="iconList"></home-icons>
        <home-recommend :recommendList="recommendList"></home-recommend>
        <home-week-end :weekendList="weekendList"></home-week-end>
    </div>
</template>

<script>
import HomeHeader from "@/views/home/compoments/HomeHeader";
import HomeSwiper from "@/views/home/compoments/HomeSwiper";
import HomeIcons from "@/views/home/compoments/HomeIcons";
import HomeRecommend from "@/views/home/compoments/HomeRecommend";
import HomeWeekEnd from "@/views/home/compoments/HomeWeekEnd";
import axios from "axios";
export default {
  name: "Home",
  components: {
    HomeHeader,
    HomeSwiper,
    HomeIcons,
    HomeRecommend,
    HomeWeekEnd
  },
  data() {
    return {
      swiperList: [],
      iconList: [],
      recommendList: [],
      weekendList: []
    };
  },
  methods: {
    getHomeInfo() {
      axios.get("/api/index").then(this.getHomeInfoSucc);
    },
    getHomeInfoSucc(res) {
      res = res.data;
      if (res.ret && res.data) {
        const data = res.data;
        this.swiperList = data.swiperList;
        this.iconList = data.iconList;
        this.recommendList = data.recommendList;
        this.weekendList = data.weekendList;
      }
    }
  },
  mounted() {
    this.getHomeInfo();
  }
};
</script>
